import React, { useEffect, useState } from "react";
import { HavnAIPrompt } from "../components/HavnAIPrompt";
import { HavnAIButton } from "../components/HavnAIButton";
import { StatusBox } from "../components/StatusBox";
import { OutputCard } from "../components/OutputCard";
import { HistoryFeed, HistoryItem } from "../components/HistoryFeed";
import { submitAutoJob, fetchJob, fetchResult } from "../lib/havnai";

const HISTORY_KEY = "havnai_test_history_v1";

const MODEL_OPTIONS: { id: string; label: string }[] = [
  { id: "auto", label: "Auto (let grid choose best)" },
  { id: "majicmixRealistic_v7", label: "majicmixRealistic_v7 · all‑round realism" },
  { id: "lazymixRealAmateur_v40", label: "lazymixRealAmateur_v40 · phone-photo realism" },
  { id: "juggernautXL_ragnarokBy", label: "juggernautXL_ragnarokBy · SDXL studio" },
  { id: "epicrealismXL_vxviiCrystalclear", label: "epicrealismXL_vxviiCrystalclear · SDXL daylight" },
  { id: "perfectdeliberate_v5SD15", label: "perfectdeliberate_v5SD15 · portraits" },
  { id: "uberRealisticPornMerge_v23Final", label: "uberRealisticPornMerge_v23Final · glossy studio" },
  { id: "triomerge_v10", label: "triomerge_v10 · fantasy stylized" },
  { id: "unstablePornhwa_beta", label: "unstablePornhwa_beta · manhwa" },
  { id: "disneyPixarCartoon_v10", label: "disneyPixarCartoon_v10 · cartoon" },
  { id: "kizukiAnimeHentai_animeHentaiV4", label: "kizukiAnimeHentai_animeHentaiV4 · anime" },
];

const TestPage: React.FC = () => {
  const [prompt, setPrompt] = useState("");
  const [jobId, setJobId] = useState<string | undefined>();
  const [statusMessage, setStatusMessage] = useState<string | undefined>();
  const [loading, setLoading] = useState(false);
  const [imageUrl, setImageUrl] = useState<string | undefined>();
  const [model, setModel] = useState<string | undefined>();
  const [runtimeSeconds, setRuntimeSeconds] = useState<number | null>(null);
  const [history, setHistory] = useState<HistoryItem[]>([]);
  const [advancedOpen, setAdvancedOpen] = useState(false);
  const [selectedModel, setSelectedModel] = useState<string>("auto");

  // Load history from localStorage on mount
  useEffect(() => {
    if (typeof window === "undefined") return;
    try {
      const raw = window.localStorage.getItem(HISTORY_KEY);
      if (!raw) return;
      const parsed = JSON.parse(raw);
      if (Array.isArray(parsed)) {
        setHistory(parsed);
      }
    } catch {
      // ignore
    }
  }, []);

  const saveHistory = (items: HistoryItem[]) => {
    setHistory(items);
    if (typeof window !== "undefined") {
      window.localStorage.setItem(HISTORY_KEY, JSON.stringify(items));
    }
  };

  const handleSubmit = async () => {
    const trimmed = prompt.trim();
    if (!trimmed) return;
    setLoading(true);
    setStatusMessage("Job submitted…");
    setImageUrl(undefined);
    setRuntimeSeconds(null);
    setModel(undefined);
    setJobId(undefined);

    try {
      const id = await submitAutoJob(
        trimmed,
        selectedModel === "auto" ? undefined : selectedModel
      );
      setJobId(id);
      setStatusMessage("Waiting for GPU node…");
      await pollJob(id, trimmed);
    } catch (err: any) {
      setStatusMessage(err?.message || "Failed to submit job.");
    } finally {
      setLoading(false);
    }
  };

  const pollJob = async (id: string, usedPrompt: string) => {
    const start = Date.now();
    let attempts = 0;

    while (attempts < 120) {
      attempts += 1;
      try {
        const job = await fetchJob(id);
        const status = (job.status || "").toUpperCase();

        if (status === "QUEUED") {
          setStatusMessage("Job queued…");
        } else if (status === "RUNNING") {
          setStatusMessage("Rendering on HavnAI node…");
        } else if (status === "SUCCESS" || status === "COMPLETED") {
          setStatusMessage("Finalizing output…");

          // Compute runtime from timestamps if available
          let runtime: number | null = null;
          if (
            typeof job.timestamp === "number" &&
            typeof job.completed_at === "number"
          ) {
            runtime = Math.max(0, job.completed_at - job.timestamp);
          }

          const result = await fetchResult(id);
          if (!result.image_url) {
            setStatusMessage("Job finished, but no image was found.");
            return;
          }

          const resolvedUrl = result.image_url;
          setImageUrl(resolvedUrl);
          setRuntimeSeconds(runtime);
          setModel(job.model);
          setStatusMessage("Done.");

          const item: HistoryItem = {
            jobId: id,
            prompt: usedPrompt,
            imageUrl: resolvedUrl,
            model: job.model,
            timestamp: Date.now(),
          };
          const next = [item, ...history].slice(0, 5);
          saveHistory(next);
          return;
        } else if (status === "FAILED") {
          setStatusMessage("Job failed on the grid.");
          return;
        } else {
          setStatusMessage(`Status: ${status || "Unknown"}`);
        }
      } catch (err: any) {
        setStatusMessage(err?.message || "Error while polling job.");
      }

      await new Promise((resolve) => setTimeout(resolve, 1500));
    }

    const elapsed = (Date.now() - start) / 1000;
    setStatusMessage(`Gave up after ${elapsed.toFixed(1)} seconds without completion.`);
  };

  const handleHistorySelect = (item: HistoryItem) => {
    setImageUrl(item.imageUrl);
    setModel(item.model);
    setRuntimeSeconds(null);
    setJobId(item.jobId);
    setStatusMessage("Showing from history. Generate again to refresh.");
  };

  const handleHistoryClear = () => {
    saveHistory([]);
    setImageUrl(undefined);
    setModel(undefined);
    setRuntimeSeconds(null);
    setJobId(undefined);
    setStatusMessage(undefined);
  };

  // Simple particle field like the homepage
  useEffect(() => {
    if (typeof window === "undefined") return;
    const container = document.querySelector(".particle-field") as HTMLElement | null;
    if (!container) return;
    if (container.childElementCount > 0) return;
    for (let i = 0; i < 60; i += 1) {
      const dot = document.createElement("span");
      dot.style.position = "absolute";
      dot.style.width = "3px";
      dot.style.height = "3px";
      dot.style.borderRadius = "999px";
      dot.style.background = "rgba(0,208,255,0.8)";
      dot.style.left = `${Math.random() * 100}%`;
      dot.style.bottom = `${Math.random() * 100}%`;
      dot.style.opacity = String(Math.random());
      dot.style.animation = `float ${10 + Math.random() * 12}s linear infinite`;
      dot.style.animationDelay = `${Math.random() * 10}s`;
      container.appendChild(dot);
    }
  }, []);

  return (
    <div className="relative min-h-screen bg-gradient-to-b from-[#091823] via-[#030a0f] to-[#01060a] text-slate-100">
      <style jsx global>{`
        @keyframes float {
          0% {
            transform: translateY(0);
            opacity: 0;
          }
          25% {
            opacity: 0.8;
          }
          100% {
            transform: translateY(-260px);
            opacity: 0;
          }
        }
      `}</style>
      <div className="particle-field absolute inset-0 pointer-events-none" />

      <div className="relative z-10 px-6 pt-20 pb-10 max-w-md mx-auto">
        {/* Header */}
        <div className="text-center">
          <h1 className="text-3xl font-semibold text-white tracking-wide">
            Create Something Amazing.
          </h1>
          <p className="text-center text-slate-300 text-sm mt-2">
            Type a description and let the HavnAI grid bring it to life.
          </p>
        </div>

        {/* Prompt + Button */}
        <HavnAIPrompt
          value={prompt}
          onChange={setPrompt}
          onSubmit={handleSubmit}
          disabled={loading}
        />

        <HavnAIButton
          label="Generate"
          loading={loading}
          disabled={!prompt.trim()}
          onClick={handleSubmit}
        />

        {/* Advanced controls */}
        <div className="mt-3">
          <button
            type="button"
            className="text-[11px] text-slate-400 hover:text-slate-200 underline-offset-2 hover:underline"
            onClick={() => setAdvancedOpen((v) => !v)}
          >
            {advancedOpen ? "Hide advanced options" : "Show advanced options"}
          </button>

          {advancedOpen && (
            <div className="mt-3 p-3 rounded-xl bg-[rgba(255,255,255,0.05)] border border-[rgba(255,255,255,0.08)] text-xs text-slate-200">
              <label className="block text-[11px] uppercase tracking-[0.15em] text-slate-400 mb-1">
                Model
              </label>
              <select
                value={selectedModel}
                onChange={(e) => setSelectedModel(e.target.value)}
                className="w-full bg-[rgba(0,0,0,0.5)] border border-[rgba(255,255,255,0.16)] rounded-lg px-3 py-2 text-xs focus:outline-none focus:ring-2 focus:ring-cyan-400/60"
              >
                {MODEL_OPTIONS.map((opt) => (
                  <option key={opt.id} value={opt.id}>
                    {opt.label}
                  </option>
                ))}
              </select>
              <p className="mt-2 text-[10px] text-slate-400">
                Auto will route to the highest-performing model based on weight and pipeline. Choosing a specific model overrides auto routing for this job only.
              </p>
            </div>
          )}
        </div>

        {/* Status */}
        <StatusBox message={statusMessage} />

        {/* Output */}
        <OutputCard
          imageUrl={imageUrl}
          model={model}
          runtimeSeconds={runtimeSeconds || null}
          jobId={jobId}
        />

        {/* History */}
        <HistoryFeed
          items={history}
          onSelect={handleHistorySelect}
          onClear={handleHistoryClear}
        />
      </div>
    </div>
  );
};

export default TestPage;
